<template>
    <nav v-if="isLoggedIn">
        <div class="nav-wrapper yellow">
            <div class="container">
                <router-link v-if="isLoggedIn" to="/" class="brand-logo black-text">
                    READys
                </router-link>
                <ul class="right">
                    <!-- <li v-if="isLoggedIn"><router-link to="/users" class="black-text">Users</router-link></li>
                    <li v-if="isLoggedIn"><router-link to="/teachers" class="black-text">Teachers</router-link></li>
                    <li v-if="isLoggedIn"><router-link to="/vocabulary" class="black-text">Vocabulary</router-link></li>
                    <li v-if="isLoggedIn"><router-link to="/myprofile" class="black-text">My Profile</router-link></li> -->
                    <!-- <li v-if="isLoggedIn"><span class="email black-text">{{ currentUser }}</span></li> -->
                    <!-- <li v-if="isLoggedIn"><router-link to="/" class="black-text">Dashboard</router-link></li>
                    <li v-if="!isLoggedIn"><router-link to="/login" class="black-text">Login</router-link></li>
                    <li v-if="!isLoggedIn"><router-link to="/register" class="black-text">Register</router-link></li> -->
                    <li v-if="isLoggedIn"><button v-on:click="logout" class="btn blak">Logout</button></li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
import firebase,{ functions } from 'firebase'

export default {
    name: 'navbar',
    data() {
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
        if(firebase.auth().currentUser) {
            this.isLoggedIn = true;
            this.currentUser = firebase.auth().currentUser.email
        }
    },
    methods: {
        logout: function() {
            firebase
            .auth()
            .signOut()
            .then(() => {
                this.$router.go({ path: this.$router.path });
            });
        }
    }
}
</script>

<style scoped>
    .email {
        padding-right: 10px;
    }
</style>



