<template>
    <div id="edit-user">
        <h3>Edit User</h3>
        <div class="row">
            <form @submit.prevent="updateUser" class="col s12">
                <div class="row">
                    <div class="input-fields col s12">
                        <input type="text" v-model="fname" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-fields col s12">
                        <input type="text" v-model="lname" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-fields col s12">
                        <input type="text" v-model="username" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-fields col s12">
                        <input type="text" v-model="password" required>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/users" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'edit-user',
    data () {
        return {
            user_id: null,
            fname: null,
            lname: null,
            username: null,
            password: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('users')
        .where('user_id','==', to.params.user_id)
        .get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.user_id = doc.data().user_id
                    vm.fname = doc.data().fname
                    vm.lname = doc.data().lname
                    vm.username = doc.data().username
                    vm.password = doc.data().password
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('users')
            .where('user_id', '==', this.$route.params.user_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.user_id = doc.data().user_id
                    this.fname = doc.data().fname
                    this.lname = doc.data().lname
                    this.username = doc.data().username
                    this.password = doc.data().password
                })
            })
        },
        updateUser () {
            db.collection('users')
            .where('user_id', '==', this.$route.params.user_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        user_id: this.user_id,
                        fname: this.fname,
                        lname: this.lname,
                        username: this.username,
                        password: this.password
                    })
                    .then(() => {
                        this.$router.push({name: 'users', params: {user_id: this.user_id}})
                    })
                })
            })
        }
    }
}
</script>



