<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Users</h4>
            </li>

            <div class="padd">
                <div>
                    <table class="centered">
                        <thead>
                            <tr>
                                <th>First Name</th>
                                <th>Last Name</th>
                                <th>Username</th>
                                <th>Passwors</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody v-for="user in users" v-bind:key="user.user_id">
                            <tr>
                                <td>{{ user.fname }}</td>
                                <td>{{ user.lname }}</td>
                                <td>{{ user.username }}</td>
                                <td>{{ user.password }}</td>
                                <td>
                                    <router-link v-bind:to="{name: 'edit-user', params: {user_id: user.user_id}}" class="btn blue">
                                        <i class="fa fa-pencil"></i>
                                    </router-link>
                                    <button @click="deleteEmployee" class="btn red">
                                        <i class="fa fa-trash"></i>
                                    </button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </ul>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'users',
    data () {
        return{
            users: []
        }
    },
    created () {
        db.collection('users')
        .orderBy('user_id')
        .get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'user_id': doc.data().user_id,
                    'fname' : doc.data().fname,
                    'lname' : doc.data().lname,
                    'username' : doc.data().username,
                    'password' : doc.data().password
                }
                this.users.push(data)
            })
        })
    },
    // methods: {
    //     deleteEmployee () {
    //     if(confirm('Are you sure?')) {
    //         db.collection('users')
    //     .where('user_id', '==', this.$route.params.user_id)
    //     .get()
    //     .then(querySnapshot => {
    //         querySnapshot.forEach(doc => {
    //             doc.ref.delete();
    //             this.$router.push('/users')
    //         })
    //     })
    //     }
    // }
    // }
}
</script>

<style scoped>
.padd{
    padding-left: 100px;
    padding-right: 100px;
}
</style>



